import React from "react";
import '../DataCss/Footer.css'
import Img from "../DataImage/one.png"
import Imgg from "../DataImage/two.png"
import Imggg from "../DataImage/three.png"
import {Formik} from 'formik';
import * as Yup from 'yup';


const Buy =()=>{



    window.scrollTo(0,0)



    return(


        <div className="flex topBuy">
            <Formik 
       initialValues={{
       cartname: "",
       cartnamea: "",
       cartpassword: "",
       cartpassworda: "",
       cartpasswordb: "",
       cartpasswordc: "",
       cartagree:"false"
       }} 
       validationSchema={
       Yup.object({
           cartname: Yup.string().required("Ad bo ola bilmz!")
           .min(4, 'Ad 4 hrfdn az ola bilmz!')
           .max(30, 'Ad 30 hrfdn ox ola bilmz!'),
           cartnamea: Yup.string().required("Soyad bo ola bilmz!")
           .min(4, 'Soyad 4 hrfdn az ola bilmz!')
           .max(30, 'Soyad 30 hrfdn ox ola bilmz!'),
           cartpassword: Yup.number("")
           .typeError("Kart nmrsi hrflrdn  v simvollardan ibart ola bilmz")
           .required('Kart nmrsi tyin olunmayb!') ,
           cartpassworda: Yup.number("")
           .typeError("Kart nmrsi hrflrdn  v simvollardan ibart ola bilmz")
           .required('Kart nmrsi tyin olunmayb!') ,
           cartpasswordb: Yup.number("")
           .typeError("Son istifad tarixi hrflrdn v simvollardan ibart ola bilmz")
           .required('Son istifad tarixi tyin olunmayb!') ,
           cartpasswordc: Yup.number("")
           .typeError("Thlksizlik kodu hrflrdn v simvollardan ibart ola bilmz")
           .required('Thlksizlik kodu tyin olunmayb!') ,
         cartagree: Yup.bool().oneOf([true], 'Qaydalar qbul etmlisiniz!')
       })
       }
       onSubmit={(values, {resetForm, setSubmitting})=>{
        }}
       
       >
           {({values, errors, handleSubmit, handleReset, handleChange, dirty, isSubmitting, touched})=>(
<form className="payBuy only-column" onSubmit={handleSubmit} autocomplete="off">
<span className="only-row buyIconBuc">
<img src={Img}/>
<img src={Imgg}/>
<img src={Imggg}/>
</span>
<input type="text" id="cartname" onChange={handleChange} value={values.cartname} className="buyInputCart" placeholder="First Name"/>
{errors.cartname && touched.cartname &&(<span className="emailCart">{errors.cartname}</span>)}

<input type="text" id="cartnamea" onChange={handleChange} value={values.cartnamea} className="buyInputCart" placeholder="Last Name"/>
{errors.cartnamea && touched.cartnamea &&(<span className="emailCart">{errors.cartnamea}</span>)}

<input type="text" id="cartpassword"  minLength="16" maxLength="16" onChange={handleChange} value={values.cartpassword} className="buyInputCart" placeholder="Cart Number"/>
{errors.cartpassword && touched.cartpassword &&(<span className="emailCart">{errors.cartpassword}</span>)}

<input type="text" id="cartpassworda"  minLength="16" maxLength="16" onChange={handleChange} value={values.cartpassworda} className="buyInputCart" placeholder="Cart Number"/>
{errors.cartpassworda && touched.cartpassworda &&(<span className="emailCart">{errors.cartpassworda}</span>)}

<input type="text" id="cartpasswordb"  minLength="4" maxLength="4" onChange={handleChange} value={values.cartpasswordb} className="buyInputCart" placeholder="Expiration Date (MM/YY)"/>
{errors.cartpasswordb && touched.cartpasswordb &&(<span className="emailCart">{errors.cartpasswordb}</span>)}

<input type="text" id="cartpasswordc"  minLength="3" maxLength="3" onChange={handleChange} value={values.cartpasswordc} className="buyInputCart" placeholder="Security code (CVV)"/>
{errors.cartpasswordc && touched.cartpasswordc &&(<span className="emailCart">{errors.cartpasswordc}</span>)}

<span className="only-row getChek">
<input id ="Iagree" id="cartagree" onChange={handleChange} value={values.cartagree} type="checkbox"/>
<label className="labelAgree"  for="Iagree">I Agree</label>
</span>
{errors.cartagree && touched.cartagree &&(<span className="emailCart">{errors.cartagree}</span>)}

<button className="BuyButtonMember flex">TSDQL</button>
</form> )}
   </Formik>
        </div>
    )
}


export default Buy;