import React from 'react';
import { useSelector,useDispatch } from 'react-redux';
import '../DataCss/BodyOne.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { NavLink } from 'react-router-dom';


const Romantika = ()=>{

  const selec = useSelector(state => state.storeOne)
  const imgUrl = 'https://image.tmdb.org/t/p/w500'


  const dispatch = useDispatch();
  const selDaxil = useSelector(state => state.later);
  const selDaxill = useSelector(state => state.bucket);
  
  const handleClick = (kino) =>{
    const daxill = selDaxil.filter(index=> Number(index.id) === Number(kino.id))
  if(daxill.length == 0){
    dispatch({
      type: "KINO",
      payload: kino 
    })}}
    
  const handleSeb = (qiymet)=>{
    const daxil = selDaxill.filter(index=> Number(index.id) === Number(qiymet.id))
    if(daxil.length !== 0){
      daxil[0].length += 1
    }else{
      dispatch({
        type: "Qiymet",
        payload: qiymet
      })}}


      const handleSebet = (item,element) => {
        if(element.target.style.border == "1px solid white" || element.target.style.border == ""){
          element.target.style.border = "1px solid red"
          element.target.style.color = "red"
          handleSeb(item)
        } else {
          element.target.style.border = "1px solid white"
          element.target.style.color = "white"
          dispatch({type: "Del", payload: item.id})
        }
      }
      const handleSebett = (item,element) => {
        if(element.target.style.border == "1px solid white" || element.target.style.border == ""){
          handleClick(item)
          element.target.style.border = "1px solid red"
          element.target.style.color = "red"
        } else {
          element.target.style.border = "1px solid white"
          element.target.style.color = "white"
          dispatch({type: "Sil", payload: item.id})
        }
      }




    return(
        <div className="oneLeaf oneleafImport">
{
selec[0] && selec[0].map(index =>(

<>{(index.genre_ids.map(e=>{
  if(e===10749)
return <div key={index.id} className="onlycolumn styleMovie">
<div className="imgDiv"><NavLink to="/eachMovie" onClick={() => dispatch({type: "Each", payload: index})}>
<img src={imgUrl + index.poster_path}/>
<div className="posit">
<FontAwesomeIcon size="7x" icon="fas fa-play-circle"/>
</div>
<span className="between spanPos">
<p className="pOne">{index.release_date.slice(0,4)}</p>
<p className="pOneee">
  <FontAwesomeIcon style={{margin:"0 5px 0 0"}} className="svgPos" icon="fas fa-dollar-sign"/>
  {String(index.vote_count).slice(0,2)}
</p>
<p className="pOnee">
<FontAwesomeIcon style={{margin:"0 5px 0 0"}} className="svgPos" icon="fas fa-star"/>
  {index.vote_average}
</p>

</span>
<p className="row genreId">{(index.genre_ids.map(e=>{
if(e===28)
return e ="Aksiyon"
if(e===12)
return e ="Macra"
if(e===16)
return e ="Animasiya"
if(e===35)
return e ="Komediya"
if(e===80)
return e ="Cinayt"
if(e===99)
return e ="Sndli"
if(e===18)
return e ="Dram"
if(e===10751)
return e ="Ail"
if(e===14)
return e ="Fantaziya"
if(e===36)
return e ="Tarix"
if(e===27)
return e ="Qorxu"
if(e===10402)
return e ="Musiqi"
if(e===10749)
return e ="Ronamtika"
if(e===878)
return e ="Elm"
if(e===10752)
return e ="Mharib"
if(e===37)
return e ="Qrb"
if(e===53)
return e ="Triller"
if(e===9648)
return e ="Sirr"
})).join(', ')}</p></NavLink>
</div>
<div className="titleOne">
<span className="between spanPosss">
<p className="pOneTwo"  onClick={(e)=> handleSebet(index,e)}  >
<FontAwesomeIcon   className="svgPos"  icon="fas fa-shopping-cart"/>
</p>
<p className="pOneThree" onClick={(e)=> handleSebett(index,e)}   >
<FontAwesomeIcon className="svgPos"  icon="fas fa-clock"/>
</p>
</span>
<NavLink to="/eachMovie" onClick={() => dispatch({type: "Each", payload: index})}>
  <p className="pTwo">{index.title}</p>
  </NavLink>

</div>



</div>
  }))} </>
))


}        </div>
    );
}

export default Romantika